import React, { StyleSheet } from "react";

const style = {
  color: "white",
  backgroundColor: "red",
  textDecoration: "none",
  padding: "10px"
};

export default function Link(props) {
  return (
    <a style={style} rel="noreferrer" target="_blank" href={props.href}>
      {props.text}
    </a>
  );
}
