import Parse from "parse";

Parse.serverURL = "https://parseapi.back4app.com";
Parse.initialize(
  "2hnNKpCJvxK5wPCaJqrSvjS0ofLwJoY76UPN9V3Y",
  "KNkCII8G5ckXf92iLaPKve8JDMofXPxOX87aDKE7"
);

const Link = Parse.Object.extend("Link");
const query = new Parse.Query(Link);

export default class Backend {
  static async getAll() {
    const links = await query.findAll();
    return JSON.parse(JSON.stringify(links));
  }
}
